import config from "@/config/common.config"
import {changeTime} from 'ais-common-utils'
import {inCome, pushList, withDrawList} from '~/api/pushSell'
export default {
  data() {
    return {
      info: {},
      list: [],
      list2: [],
      page:1,
      pageSize:10,
      page2:1,
      pageSize2:10,
      loading: false,
      finished: false,
      loading2: false,
      finished2: false,
      showTable:1, // 1是提现，2是收益明细
      config,
      changeTime,
      show_explain:false,
    }
  },
  watch: {
    loading(){
      this.loading && this.page++
    },
    loading2(){
      this.loading2 && this.page2++
    }
  },
  mounted() {
    this.getIncome()
  },
  methods: {
    handleWithDraw(){
      if(this.info.canWithdraw <= 0){
        this.$toast('可提现金额不足')
        return 
      }
      this.$router.push('/pushSell/withDraw')
    },
    async getIncome () {
      const {data} = await inCome()
      this.info = data.data
    },
    // 收益列表
    async getList () {
      const {data} = await pushList({
        page:this.page,
        pageSize:this.pageSize,
      })
      if(data.data.entries.length){
        this.list = this.list.concat(data.data.entries)
        this.loading = false
      }else{
        this.finished = true
      }
    },
    // 提现记录列表
    async getwithDrawList(){
      const {data} = await withDrawList({
        page:this.page2,
        pageSize:this.pageSize2,
      }) 
      if(data.data.entries.length){
        this.list2 = this.list2.concat(data.data.entries)
        this.loading2 = false
      }else{
        this.finished2 = true
      } 
    },
  },
  head: {
    title: '推广收益—艾思云课堂',
  },
}
