// 分销推广
import request from "@/utils/request"
import qs from "qs"
// 账户收益
export const inCome = () => {
  return request.post('/school/user/promotion/income')
}
// 推送列表
export const pushList = (params) => {
  return request.post('/school/user/promotion/list', qs.stringify(params))
}
// 提现到微信
export const withDraw = (params) => {
  return request.post('/school/user/promotion/withdraw', qs.stringify(params))
}
// 提现列表
export const withDrawList = (params) => {
  return request.post('/school/user/promotion/withdrawList', qs.stringify(params))
}
// 提现验签
export const withDrawSign = (params) => {
  return request.post('/school/user/promotion/sign', qs.stringify(params))
}
//提现历史账号记录
export const withDrawHistory = () => {
  return request.post('/school/user/promotion/history')
}
// 微信授权
export const authorize = (type) => {
  return request.get(`/school/common/mp/authorize?type=${type}`)
}